import React, { Component,Suspense} from "react";
import "./App.css";
import { Switch, Route} from "react-router-dom";
import Navigation from './Component/Navigation/Navigation';
import Footer from './Component/Footer/Footer'
export default class App extends Component {
  render() {
    const Home =React.lazy(() => import('./Pages/Home'));
    const Contact =React.lazy(() => import('./Pages/Contact'));
    const About =React.lazy(() => import('./Pages/About'));
    const Pub =React.lazy(() => import('./Pages/pub'));
    const Blog =React.lazy(() => import('./Pages/Blog'));
    const Login =React.lazy(() => import('./Pages/Login'));
    const Portfolio =React.lazy(() => import('./Pages/Portfolio'));
    const Privacy =React.lazy(() => import('./Component/Privacy-Condition/Privacy'));
    const Terms =React.lazy(() => import('./Component/Privacy-Condition/Policy'));
    return (
      <div>
    <Suspense fallback={'Loading...'}>
      <div className='nav-app'>
      <Navigation />
      </div>
      <Switch>
        <Route exact path="/" component={Home}/>
        <Route exact path="/contact" component={Contact}/>
        <Route exact path="/about" component={About}/>
        <Route exact path="/privacy-policy" component={Privacy}/>
        <Route exact path="/terms-condition" component={Terms}/>
        <Route exact path="/publication" component={Pub}/>
        <Route exact path="/blog" component={Blog}/>
        <Route exact path="/Login" component={Login}/>
        <Route exact path="/portfolio" component={Portfolio}/>
      </Switch>
      <div>
      <Footer />
      </div>
    </Suspense>
      </div>
    );
  }
}
